/* Quiz.css */

/* Importing macOS-like font. Note: SF Pro Display is proprietary.
   Use a similar free font or host SF Pro if licensed. */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif; /* Fallback if SF Pro is unavailable */
  background-color: #f5f5f7; /* Light gray typical of macOS backgrounds */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.quiz-container {
  width: 100%;
  max-width: 500px;
  padding: 20px;
}

.quiz-card {
  background-color: #ffffff;
  border-radius: 16px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.1);
  padding: 30px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.quiz-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 28px rgba(0, 0, 0, 0.15);
}

.quiz-title {
  text-align: center;
  font-size: 28px;
  font-weight: 700;
  color: #1c1c1e;
  margin-bottom: 25px;
}

.score-section {
  text-align: center;
  font-size: 22px;
  color: #1c1c1e;
}

.score-section span {
  color: #007aff; /* macOS blue accent */
  font-weight: 500;
}

.quiz-section {
  display: flex;
  flex-direction: column;
}

.question-section {
  margin-bottom: 20px;
}

.question-count {
  font-size: 14px;
  color: #8e8e93;
  margin-bottom: 8px;
}

.question-text {
  font-size: 20px;
  color: #1c1c1e;
  font-weight: 500;
}

.answer-section {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 25px;
}

.option-button {
  padding: 12px 20px;
  background-color: #f2f2f7;
  border: 1px solid #c7c7cc;
  border-radius: 12px;
  font-size: 16px;
  color: #1c1c1e;
  cursor: pointer;
  transition: background-color 0.2s ease, border-color 0.2s ease, color 0.2s ease;
}

.option-button:hover:not(:disabled) {
  background-color: #e5e5ea;
}

.option-button.selected {
  background-color: #007aff;
  border-color: #007aff;
  color: #ffffff;
}

.option-button:disabled {
  cursor: not-allowed;
  opacity: 0.8;
}

.next-button {
  align-self: flex-end;
  padding: 12px 24px;
  background-color: #007aff;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  color: #ffffff;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.next-button:hover {
  background-color: #0051a8;
}

/* Responsive Design */
@media (max-width: 600px) {
  .quiz-card {
    padding: 20px;
  }

  .quiz-title {
    font-size: 24px;
  }

  .question-text {
    font-size: 18px;
  }

  .option-button {
    font-size: 14px;
    padding: 10px 16px;
  }

  .next-button {
    font-size: 14px;
    padding: 10px 20px;
  }
}