@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
/* Reset some default browser styles */
body, html, #root {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: Arial, sans-serif;
  background-color: #ffffff;
  color: #333333;
}
.sidebar {
  width: 200px;
  background-color: #f8f8f8;
  border-right: 1px solid #e7e7e7;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  height: 100%; /* Ensure the sidebar takes full height */
}

.sidebar-title {
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 1.5em;
  color: #555555;
  flex: 0 0 auto; /* Prevent the title from stretching */
}

.sidebar-list-container {
  flex: 1 1 auto; /* Allow this container to grow and fill available space */
  display: flex;
  flex-direction: column;
  justify-content: center; /* Vertically center the list */
  align-items: start; /* Optional: Horizontally center the list items */
}

.sidebar-list {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%; /* Make the list take full width if needed */
}

.sidebar-item {
  padding: 10px 0;
  cursor: pointer;
  color: #555555;
  transition: color 0.3s, opacity 0.3s, font-size 0.3s;
  opacity: 0.7;
  font-size: 1em;
  text-align: center; /* Centers text horizontally within each item */
}

.sidebar-item:hover {
  color: #000000;
}

.sidebar-item.active {
  font-size: 1.2em;
  opacity: 1;
  color: blue;
}
.main-content {
    flex: 1 1;
    padding: 40px;
    box-sizing: border-box;
  }
  
  .main-content h1 {
    margin-top: 0;
    color: #333333;
  }
  
  .main-content p {
    font-size: 1.1em;
    line-height: 1.6;
    color: #555555;
  }
/* src/components/Introduction.css */

.introduction {
    padding: 20px;
    background-color: #ffffff;
    color: #333333;
  }
  
  .introduction h2 {
    color: #555555;
    margin-bottom: 20px;
  }
  
  .introduction p {
    font-size: 1.1em;
    line-height: 1.6;
    margin-bottom: 20px;
  }
  
  /* Container for the cards */
  .cards-container {
    display: flex;
    flex-wrap: wrap; /* Allows cards to wrap on smaller screens */
    grid-gap: 20px;
    gap: 20px;       /* Space between cards */
    justify-content: flex-start; /* Left justified */
  }
  
  /* Card styling */
  .card {
    background-color: #f9f9f9;
    border: 1px solid #e7e7e7;
    border-radius: 8px;
    padding: 20px;
    width: 500px; /* Adjust as needed */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    cursor: pointer; /* Indicates the card is clickable */
    transition: transform 0.2s, box-shadow 0.2s;
  }
  
  .card:hover {
    transform: translateY(-5px);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
  }
  
  .card h3 {
    margin-top: 0;
    color: #333333;
  }
  
  .card-image {
    display: block;
    margin: 10px 0;
    max-width: 100%;
    height: auto;
  }
  
  .card p {
    color: #555555;
    font-size: 1em;
    line-height: 1.5;
  }
